.wrapper__title__text {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.wrapper__title {
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}

.wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}
.wrapper__title {
  display: flex;
  align-items: center;
  padding: 2em;
  background-color: #027dbf;
}
.wrapper__title__text {
  display: flex;
  align-items: center;
  color: #ffffff !important;
  margin: 0 !important;
  margin-right: 4em !important;
}
.wrapper__title__text__logo {
  width: 1.5em !important;
  height: 1.5em !important;
  margin-right: calc(1em * 1 / 1.5) !important;
}
.wrapper__title__search {
  flex-grow: 1;
}
.wrapper__body {
  flex-grow: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  max-width: 1280px;
}
.wrapper__body__header {
  margin: 2em 0;
  padding: 0 2em;
}
.wrapper__body__results {
  padding: 0 2em;
  flex-grow: 1;
  overflow-y: auto;
}
.wrapper__body__results::after {
  display: block;
  content: "";
  height: 2em;
}
.wrapper__body__results__items {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  column-gap: 1em;
  row-gap: 1em;
}

